@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html, body {
  font-family: "Roboto", sans-serif;
  min-height: 100%; }

.grid-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #586981; }

.cv-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  margin: 5rem;
  padding: 2.5rem;
  min-width: 600px;
  height: 600px;
  border: 1px solid black;
  background-color: whitesmoke;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75); }

.section-title {
  font-size: 2rem;
  font-weight: 700;
  margin-top: 1rem;
  margin-bottom: 1.5rem; }

.section {
  display: flex;
  align-items: baseline;
  color: #4D708B; }
  .section :nth-child(2) {
    margin-left: .5rem;
    cursor: pointer; }

.header {
  grid-column: 1 / -1;
  margin: 1rem;
  font-weight: 300; }
  .header .name {
    font-size: 3rem;
    font-weight: 700; }
  .header .email {
    font-size: 1.1rem; }
  .header .phone {
    font-size: 1.1rem;
    margin-top: .4rem; }

.education {
  grid-column: 1 / -1;
  margin: 1rem; }
  .education .education-content {
    display: flex;
    justify-content: space-between;
    align-items: baseline; }
  .education .leftside {
    display: flex;
    align-items: baseline; }
    .education .leftside :nth-child(2) {
      margin-left: .5rem; }
  .education .school {
    font-size: 1.3rem; }
  .education .major {
    font-size: 1rem;
    font-weight: 300; }
  .education .graduation {
    font-size: 1.2rem;
    font-weight: 300; }

.experience {
  grid-column: 1 / -1;
  margin: 1rem; }
  .experience .experienceCard {
    margin-top: 1rem;
    margin-bottom: 1rem; }
  .experience .left-side {
    display: flex;
    align-items: baseline; }
    .experience .left-side :nth-child(2) {
      margin-left: .5rem;
      font-size: 1.1rem; }
  .experience .split-content {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    font-size: 1.3rem;
    font-weight: 500; }
  .experience .position {
    font-weight: 300; }
  .experience .dates {
    font-weight: 300; }
  .experience .duties {
    margin-top: .8rem;
    margin-bottom: .8rem;
    font-weight: 300;
    font-size: .95rem; }

form {
  background-color: white;
  border: 1px solid black;
  position: absolute;
  margin: auto;
  padding: 1.5rem;
  display: flex;
  flex-direction: column; }
  form label {
    display: flex;
    justify-content: space-between; }

